<template>
  <div id="table-card-component">
    <div class="top-container">
      <h3 class="title">{{ title }}</h3>
      <div v-if="expand" class="expand-button" @click="onExpandClick">
        <img src="@/assets/images/icons/png/expand.png" alt="" />
      </div>
    </div>
    <div class="table-container">
      <el-table
        :data="data"
        :style="{ width: 'calc(100% - 60px)' }"
        row-class-name="table-row"
        cell-class-name="table-cell"
      >
        <el-table-column
          v-for="column in columns"
          :key="column.prop"
          :label="column.label"
          :width="column.width || 'auto'"
        >
          <template #default="scope">
            <div v-if="column.prop === 'index'">
              <p>{{ scope.$index + 1 }}</p>
            </div>

            <div
              v-else-if="column.prop === 'collection'"
              class="collection-cell"
            >
              <div class="collection-wrapper">
                <CollectionCard
                  :data="scope.row.collection"
                  cover-mode
                ></CollectionCard>
              </div>
              <p>{{ scope.row.collection.name }}</p>
            </div>

            <div v-else-if="column.prop === 'fabric'" class="fabric-cell">
              <img :src="scope.row.fabric.image" alt="" />
              <p>{{ scope.row.fabric.name }}</p>
            </div>

            <div v-else-if="column.prop === 'color'" class="color-cell">
              <div
                class="circle"
                :style="{ background: scope.row.color }"
              ></div>
              <p>{{ scope.row.color }}</p>
            </div>

            <p v-else>{{ scope.row[column.prop] }}</p>
          </template>
        </el-table-column>
      </el-table>
    </div>

    <el-dialog :title="title" v-model="dialogVisible" width="50%">
      <div class="table-container">
        <el-table
          :data="data"
          :style="{ width: 'calc(100% - 60px)' }"
          row-class-name="table-row"
          cell-class-name="table-cell"
        >
          <el-table-column
            v-for="column in columns"
            :key="column.prop"
            :label="column.label"
            :width="column.width || 'auto'"
          >
            <template #default="scope">
              <div v-if="column.prop === 'fabric'" class="fabric-cell">
                <p>{{ scope.row.fabric.name }}</p>
                <img :src="scope.row.fabric.image" alt="" />
              </div>
              <p v-else>{{ scope.row[column.prop] }}</p>
            </template>
          </el-table-column>
        </el-table>
      </div>
    </el-dialog>
  </div>
</template>

<script lang="ts">
import { defineComponent, ref, PropType } from "vue";
import { Column } from "@/constants/element";
import CollectionCard from "@/components/CollectionCard.vue";

const Component = defineComponent({
  name: "TableCard",
  components: { CollectionCard },
  props: {
    title: {
      type: String,
      default: ""
    },
    columns: {
      type: Array as PropType<Column[]>,
      default: () => []
    },
    data: {
      type: Object,
      default: () => ({})
    },
    expand: {
      type: Boolean,
      default: false
    }
  },
  setup() {
    const dialogVisible = ref(false);

    const onExpandClick = () => {
      dialogVisible.value = true;
    };

    return {
      dialogVisible,

      onExpandClick
    };
  }
});

export default Component;
</script>

