<template>
  <div id="fabric-card-component">
    <div
      class="cover-image"
      :style="{
        backgroundImage: `url('${formatImageUrl(data.image_url + '@250-250')}')`
      }"
      @click="onImageClick"
    >
      <template v-if="status">
        <div v-if="data.spec_label.length" class="collection-tag">
          {{ data.spec_label.join(", ") }}
        </div>
        <div class="cover-status">
          <div class="status-tag">
            {{ status }}
          </div>
        </div>
      </template>
    </div>

    <div class="content-container">
      <div
        class="content-wrapper"
        :style="{ width: hasButtons ? '70%' : 'auto' }"
      >
        <h3 class="title">{{ data.name }}</h3>
        <p class="description">{{ data.real_composition }}</p>
      </div>
      <div v-if="hasButtons" class="buttons-group">
        <div class="button-wrapper" @click="onFavoriteClick">
          <font-awesome-icon
            v-if="data.favorite"
            class="icon heart"
            :icon="['fas', 'heart']"
          ></font-awesome-icon>
          <font-awesome-icon
            v-else
            class="icon heart"
            :icon="['far', 'heart']"
          ></font-awesome-icon>
          <p class="icon-name">Favorite</p>
        </div>
        <div class="button-wrapper" @click="onPlusClick">
          <font-awesome-icon
            class="icon plus"
            :icon="['fas', 'plus-circle']"
          ></font-awesome-icon>
          <p class="icon-name">Add</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, toRefs, PropType } from "vue";
import { useRouter } from "vue-router";
import { formatImageUrl } from "@/utils/image";
import { FabricResult } from "@/typings/fabric";
import { CollectionAddFabric } from "@/typings/collection";

const Component = defineComponent({
  name: "FabricCard",
  props: {
    data: {
      type: Object as PropType<FabricResult>,
      default: () => ({})
    },
    status: {
      type: String,
      default: ""
    },
    hasButtons: {
      type: Boolean,
      default: false
    }
  },
  setup(props, { emit }) {
    const router = useRouter();
    const { data, hasButtons } = toRefs(props);

    const onImageClick = async () => {
      await router.push({
        name: "FabricDetail",
        params: { id: data.value.id }
      });
    };

    const onFavoriteClick = async () => {
      if (!hasButtons) return;
      emit("add-to-favorite", data.value.id);
    };

    const onPlusClick = () => {
      if (!hasButtons) return;
      const params: CollectionAddFabric = {
        id: data.value.id,
        price: 0
      };
      emit("add", params);
    };

    return {
      onImageClick,
      onFavoriteClick,
      onPlusClick,
      formatImageUrl
    };
  }
});

export default Component;
</script>

