
import { defineComponent, toRefs, PropType } from "vue";
import { useRouter } from "vue-router";
import { formatImageUrl } from "@/utils/image";
import { FabricResult } from "@/typings/fabric";
import { CollectionAddFabric } from "@/typings/collection";

const Component = defineComponent({
  name: "FabricCard",
  props: {
    data: {
      type: Object as PropType<FabricResult>,
      default: () => ({})
    },
    status: {
      type: String,
      default: ""
    },
    hasButtons: {
      type: Boolean,
      default: false
    }
  },
  setup(props, { emit }) {
    const router = useRouter();
    const { data, hasButtons } = toRefs(props);

    const onImageClick = async () => {
      await router.push({
        name: "FabricDetail",
        params: { id: data.value.id }
      });
    };

    const onFavoriteClick = async () => {
      if (!hasButtons) return;
      emit("add-to-favorite", data.value.id);
    };

    const onPlusClick = () => {
      if (!hasButtons) return;
      const params: CollectionAddFabric = {
        id: data.value.id,
        price: 0
      };
      emit("add", params);
    };

    return {
      onImageClick,
      onFavoriteClick,
      onPlusClick,
      formatImageUrl
    };
  }
});

export default Component;
